<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th>商品</th>
            <th>价格</th>
            <th>加入</th>
          </tr>
        </thead>
        <tbody v-for="(pizzaOne,index) in pizza" :key="index">
            <th>
                <td>{{pizzaOne.name}}</td>
            </th>
          <tr v-for="(piz,ind) in pizzaOne.options" :key="ind">
            <td>
              <strong>{{piz.size}}</strong>
            </td>
            <td>
              <strong>{{piz.price}}</strong>
            </td>
            <td>
              <strong @click="getPizzaCount(pizzaOne.name,piz.size,piz.price)" class="btn btn-outline-success btn-sm">+</strong>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-if="!flag">购物车还没有商品</div>
    <div v-if="flag" class="col-sm-12 col-md-4">
      <table class="table">
        <thead class="thead-default">
          <tr>
            <th>数量</th>
            <th>种类</th>
            <th>价格</th>
          </tr>
        </thead>
        <tbody v-for="(pizCart,inde) in cart" :key="inde">
          <tr>
            <td>
            <button @click="pizCart.num--;deletePizza(pizCart,cart)" class="btn btn-sm">-</button>
              <strong>{{ pizCart.num }}</strong>
              <button @click="pizCart.num++" class="btn btn-sm">+</button>
            </td>
            <td>
              <strong>{{ pizCart.type }}</strong>
            </td>
            <td>
              <strong>{{ pizCart.price * pizCart.num }}</strong>
            </td>
          </tr>
           
        </tbody>
      </table>
       <p >
           总价： <span>{{ getPriceAll }}</span> RMB
       </p>
        <button class="btn btn-success btn-block">提交</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Menu",
  data() {
    return {
        flag : false
    };
  },
  computed: {
    ...mapGetters(["pizza","cart"]),
    getPriceAll(){
        let priceAll = 0;
        for(let i=0;i<this.cart.length;i++){
            priceAll += this.cart[i].num * this.cart[i].price
        }
        return priceAll
    }
  },
  beforeRouteEnter(to, from, next) {
    if (localStorage.getItem("user") != null) {
      next(vm => {
        vm.getPizzaData();
      });
    } else {
      alert("非登陆状态不能访问");
      next("/login");
    }
  },
  methods: {
    getPizzaData() {
      this.$axios.get("/wtmenu.json").then(res => {
        console.log(res);
        this.$store.dispatch("setPizzaAsync", res.data);
      });
    },
    getPizzaCount(name,size,price){
        const obj={type:name+size,price,num:1}
        this.$store.dispatch("setCartAsync",obj)
        this.flag = true;
    },
    deletePizza(obj,arr){  
        arr.forEach((element,index) => {
            // console.log(element,index)
            if(element.num <= 0){
                const arrPizza = arr.splice(index);
                // console.log(arrPizza)
            }
        });
        if(arr.length == 0){
                this.flag = false;
            }
    }
  }
};
</script>

