<template>
  <div class="row mt-3">
    <div class="col-md-12 col-lg-12">
      <div class="card">
        <div class="card-body">
          <img src="../assets/icon.png" alt class="mx-auto d-block" />
          <form @submit.prevent="handleSubmit" autocomplete="off" method="post">
            <label for="email">邮箱</label>
            <TextFiled name="email" v-model="user.email" type="email" :error="errors.email" />
            <label for="password">密码</label>
            <TextFiled
              name="password"
              v-model="user.password"
              type="password"
              :error="errors.password"
            />
            <button type="submit" class="btn btn-block btn-success">登录</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import jwt_decode from "jwt-decode";
import TextFiled from "../common/TextFiled";
export default {
  name: "Login",
  data() {
    return {
      user: {
        email: "",
        password: ""
      },
      errors: {}
    };
  },
  components: {
    TextFiled
  },
  methods: {
    handleSubmit() {
      // console.log(this.user);
      this.$axios
        .post("/users.json", this.user)
        .then(res => {
          localStorage.setItem("user", this.user.email);
          this.$store.dispatch("setUserAsync", this.user);
          // 存储isLogin的值到vuex中
          this.$store.dispatch("setIsLoginAsync", true);
          this.errors = {};
          // 页面跳转
          this.$router.push("/home");
        })
        .catch(err => {
          console.log(err);
          if (err.message) {
            this.errors = err.message;
          }
        });
    },
    // 封装方法
    isEmpty(value) {
      return (
        value === undefined ||
        value === null ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (typeof value === "string" && value.trim().length === 0)
      );
    }
  }
};
</script>

