<template>
  <div class="row mt-3">
    <div class="col-md-12 col-lg-12">
      <div class="card">
        <div class="card-body">
          <img src="../assets/icon.png" alt class="mx-auto d-block" />
          <form @submit.prevent="handleSubmit" autocomplete="off" method="post">
            <label for="email">邮箱</label>
            <TextFiled name="email" v-model="newUser.email" type="email" :error="errors.email" />
            <label for="password">密码</label>
            <TextFiled
              name="password"
              v-model="newUser.password"
              type="password"
              :error="errors.password"
            />
            <label for="confirm-password">确认密码</label>
            <TextFiled
              name="confirmPassword"
              v-model="newUser.confirmPassword"
              type="password"
              :error="errors.confirmPassword"
            />
            <button type="submit" class="btn btn-block btn-success">注册</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TextFiled from "../common/TextFiled";

export default {
  name: "Register",
  components: {
    TextFiled
  },
  data() {
    return {
      newUser: {
        email: "",
        password: "",
        confirmPassword: ""
      },
      errors: {}
    };
  },
  methods: {
    handleSubmit() {
      console.log(this.newUser);
      this.$axios
        .post("/users.json", this.newUser)
        .then(res => {
          console.log(res);
          this.errors = {};
          // 跳转到登录页面
          this.$router.push("/login");
        })
        .catch(err => {
          console.log(err);
          if (err.response.data) {
            this.errors = err.response.data;
          }
        });
    }
  }
};
</script>

