<template>
  <div class="row">
    <div class="col-sm-12 col-md-8">
      <form @submit.prevent="handleSubmit">
        <h3 class="text-muted my-5">添加新的Pizza</h3>
        <div class="form-group row">
          <label class="col-sm-1">品种</label>
          <input type="text" class="col-sm-11 form-control" name="name" v-model="pizza.name" />
        </div>
        <div class="form-group row">
          <label class="col-sm-1">描述</label>
          <textarea
            rows="5"
            class="col-sm-11 form-control"
            name="discription"
            v-model="pizza.discription"
          />
        </div>
        <p>
          <strong>选项1</strong>
        </p>
        <div class="form-group row">
          <label class="col-sm-1">尺寸</label>
          <input
            type="text"
            class="col-sm-11 form-control"
            name="size"
            v-model="pizza.options[0].size"
          />
        </div>
        <div class="form-group row">
          <label class="col-sm-1">价格</label>
          <input
            type="text"
            class="col-sm-11 form-control"
            name="price"
            v-model="pizza.options[0].price"
          />
        </div>
        <p>
          <strong>选项2</strong>
        </p>
        <div class="form-group row">
          <label class="col-sm-1">尺寸</label>
          <input
            type="text"
            class="col-sm-11 form-control"
            name="size1"
            v-model="pizza.options[1].size"
          />
        </div>
        <div class="form-group row">
          <label class="col-sm-1">价格</label>
          <input
            type="text"
            class="col-sm-11 form-control"
            name="price1"
            v-model="pizza.options[1].price"
          />
        </div>
        <button class="form-group row btn btn-success btn-block">添加</button>
      </form>
    </div>
    <!--  -->
    <!--  -->
    <!--  -->
    <div class="col-sm-12 col-md-4">
      <AddPizza />
    </div>
  </div>
</template>

<script>
import AddPizza from "./admin/AddPizza";
export default {
  name: "Admin",
  components: {
    AddPizza
  },
  data() {
    return {
      pizza: {
        name: "",
        discription: "",
        options: [{ size: "", price: "" }, { size: "", price: "" }]
      }
    };
  },
  beforeRouteEnter(to, from, next) {
    if (localStorage.getItem("user") != null) {
      next(vm => {
        vm.getPizza();
      });
    } else {
      alert("非登陆状态不能访问");
      next("/login");
    }
  },
  methods: {
    handleSubmit() {
      console.log(this.pizza.name);
      this.$axios
        .post("/wtmenu.json", this.pizza)
        .then(res => {
          //   console.log(res.config.data);
          this.errors = {};
          //   点击之后重新获取数据更新vuex
          this.$axios.get("/wtmenu.json").then(res => {
            this.$store.dispatch("setPizzaAsync", res.data);
            console.log("ww");
          });
        })
        .catch(err => {
          console.log(err);
          if (err.response.data) {
            this.errors = err.response.data;
          }
        });
    },
    getPizza() {
      this.$axios.get("/wtmenu.json").then(res => {
        console.log("来了老弟");
        this.$store.dispatch("setPizzaAsync", res.data);
      });
    }
  }
};
</script>

